<template>
  <div class="filter-wrapper">

        <yd-navbar  title="Filter">

            <router-link to="/" slot="left">
                <yd-navbar-back-icon>Return</yd-navbar-back-icon>
            </router-link>

        </yd-navbar>

    <div class="filter-item">
        <div class="smalltitle">Distric</div>

        <div>
              
            <el-radio-group v-model="distric">
                <el-radio-button label="Xuhui"></el-radio-button>
                <el-radio-button label="Pudong"></el-radio-button>
                <el-radio-button label="Jingan"></el-radio-button>
                <el-radio-button label="Huangpu"></el-radio-button>
                <el-radio-button label="Huangpu"></el-radio-button>
                <el-radio-button label="Huangpu"></el-radio-button>
                <el-radio-button label="Huangpu"></el-radio-button>
                <el-radio-button label="Huangpu"></el-radio-button>
            </el-radio-group>
          
        </div>
    </div>
      
    

    
    <div class="filter-item">
        <span class="item-title longtitle">Budget: from ¥ {{budget[0]}} to  ¥ {{ budget[1]}}  </span>

         <yd-flexbox>
            <div><div class="smalltitle">Budget : </div></div>
            <yd-flexbox-item>
                 <div class="block">
                    <el-slider
                    v-model="budget"
                    range
                    :max=35000>
                    </el-slider>
                </div>
            </yd-flexbox-item>
        </yd-flexbox>
    </div>


        <div class="smalltitle">Apartment details</div>


    <div class="filter-item">

        <span class="item-title">Rooms : </span>
    

        <el-input-number v-model="rooms" :min="1" :max="10" label="描述文字"></el-input-number>
    </div>

     <div class="filter-item">
        <span class="item-title">Bath : </span>
        <yd-spinner class="item-value" width="150px" height="40px" v-model="bath" min="1" unit="1" max="3"></yd-spinner>

    </div>



    <div class="filter-item">
        <div class="smalltitle">Facilities</div>

        <div>
        

            <el-radio-group v-model="facilities">
                <el-radio-button label="unfurnished"></el-radio-button>
                <el-radio-button label="heating"></el-radio-button>
                <el-radio-button label="open kitchen"></el-radio-button>
                <el-radio-button label="outdoor space"></el-radio-button>
                <el-radio-button label="gym"></el-radio-button>
                <el-radio-button label="parking"></el-radio-button>
                <el-radio-button label="bathtub"></el-radio-button>
                <el-radio-button label="owen"></el-radio-button>
                <el-radio-button label="dish washer"></el-radio-button>
            </el-radio-group>
            
        </div>
    </div>
    
    

      <el-button type="primary" @click.native="getFiltered">Show the matchs</el-button>

  </div>


</template>
<script>
import {mapMutations,mapGetters} from 'vuex'
export default {
    data() {
        return{
            spinner1:0,
            distric:[],
            facilities:[],
            rooms:0,
            bath:0,
            budget:[0,0],
            filters:{
                'bath_num':2
            }
        }
    },
    computed: {
        ...mapMutations(['UPDATEPOP']),
        pop:{
            get(){
                return this.$store.state.pop
            },
            set(){
                this.$store.commit('UPDATEPOP')
            }
        }
    },
    methods: {
        ...mapMutations(['UPDATE_FILTER']),
        getFiltered() {
            this.UPDATE_FILTER(this.filters)
            this.$router.push('/list')
        }
    }
}
</script>

<style lang="stylus" scoped>
.filter-wrapper
    .filter-item
        margin-top 5px
        .facilities
            .el-checkbox
                margin-bottom 10px
        .block
            margin 0 20px
        .item-options
            text-align left
            height 50px
            line-height 50px
            margin-left 8px
    .item-title
        display inline-block
        line-height 50px
        width 120px
        text-align right  
        margin-right 10px 
    .longtitle
        width 300px
    .item-value
        display inline-block
        vertical-align middle


</style>




